.contacts {
    height: 100%;
    min-height: 100vh;
    padding-top: 20px;
    padding-bottom: 5px;
    text-align: center;
    background-image: linear-gradient(
      to bottom,
      rgba(31, 31, 244, 0.5),
      rgba(214, 210, 210, 0.5)
    );
    background-size: cover;
  }
  
  @media screen and (max-width: 767px) {
    .contacts {
      padding-top: 25px;
      padding-bottom: 10px;
    }
  }
  
  @media screen and (min-width: 768px) {
    .contacts {
      padding-top: 30px;
      padding-bottom: 20px;
    }
  }
  
  .title {
    font-size: 25px;
  }
  
  @media screen and (min-width: 768px) {
    .title {
      font-size: 30px;
    }
  }
  
  @media screen and (min-width: 1200px) {
    .title {
      font-size: 35px;
    }
  }
  
  .subtitle {
    font-size: 20px;
  }
  
  @media screen and (min-width: 768px) {
    .subtitle {
      font-size: 25px;
    }
  }
  
  @media screen and (min-width: 1200px) {
    .subtitle {
      font-size: 30px;
    }
  }